<template>
  <div class="recommend">
    <div class="recommendList">
      <CardTitle>推荐歌单</CardTitle>
      <!-- <div class="update" @click="randomPersonalizeds()">查看更多</div> -->
      <keep-alive>
        <ul class="songlist">
          <SongList v-for="(item,index) in arr" :key="index" :item="item"></SongList>
        </ul>
      </keep-alive>
    </div>

    <div class="newsong">
      <CardTitle>最新音乐</CardTitle>
      <ul>
        <NewSongList
          v-for="(newSong,index) in newSongs"
          :key="index"
          :songInfo ="{isShowInfo:true,newSongItem : newSong}"
          :newSongItem="newSong"
          @current-song="$emit('current-song', $event)"
        ></NewSongList>
      </ul>
    </div>
  </div>
</template>

<script>
import CardTitle from "@/components/CardTitle.vue";
import SongList from "@/components/SongList.vue";
import NewSongList from "@/components/NewSongList.vue";

export default {
  name: "Recommend",
  components: { CardTitle, SongList, NewSongList },
  data() {
    return {
      personalizeds: [],
      newSongs: [],
      more: true,
      arr: []
    };
  },
  methods: {
    // 获取推荐歌单
    getPersonalized() {
      this.axios
        .get("/personalized")
        .then(response => {
          // console.log(response);
          this.personalizeds = response.data.result;
          this.firstSix();
          // 获取到的数据放入本地存储
          window.localStorage.setItem(
            "personalizeds",
            // 过期时间1小时
            JSON.stringify({
              expire: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data.result
            })
          );
        })
        .catch(error => {
          error;
        });
    },

    // 获取最新歌曲
    getNewSongs() {
      this.axios
        .get("/personalized/newsong")
        .then(response => {
          // console.log(response);
          this.newSongs = response.data.result;

          // 获取到的数据放入本地存储
          window.localStorage.setItem(
            "newSongs",
            // 过期时间1小时
            JSON.stringify({
              expire: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data.result
            })
          );
        })
        .catch(error => {
          error;
        });
    },

    firstSix() {
      return (this.arr = [...this.personalizeds].slice(0, 6));
    },

    // 随机取六个推荐歌单
    // randomPersonalizeds() {
    //   var arr = [...this.personalizeds];
    //   var newArr = [];
    //   for (let i = 0; i < 6; i++) {
    //     var r = Math.floor(Math.random() * arr.length);
    //     newArr.push(...arr.splice(r, 1));
    //   }
    //   return (this.arr = newArr);
    // }
  },
  created() {
    const cachePersonalizeds = JSON.parse(
      window.localStorage.getItem("personalizeds")
    );
    if (cachePersonalizeds && cachePersonalizeds.expire > Date.now()) {
      // 存在并且还没有过期
      this.personalizeds = cachePersonalizeds.result;
      this.firstSix();
    } else {
      // 已经过期
      this.getPersonalized();
    }

    const cacheNewSongs = JSON.parse(window.localStorage.getItem("newSongs"));
    if (cacheNewSongs) {
      // 存在并且还没有过期
      this.newSongs = cacheNewSongs.result;
    } else {
      // 已经过期
      this.getNewSongs();
    }

    // this.toggleMore(this.more)
  }
};
</script>

<style lang="less" scoped>
.recommend {
  margin-bottom: 47px;
}
.recommendList {
  position: relative;
}
.update {
  position: absolute;
  top: -2px;
  right: 10px;
  border: 1px solid #f5f5f5;
  border-radius: 5px;
  padding: 2px 10px;
}
.songlist {
  display: flex;
  flex-wrap: wrap;
}
</style>